.video-block{
	position: relative;
	overflow: hidden;
	border-radius: 4px;

	height: 500px;

	&::before{
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;

		z-index: 3;

		background-color: rgba($color: #1E212C, $alpha: .6);
	}

	&__content{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__play{
		@include flex-all-center;

		position: absolute;
		top: 50%;
		left: 50%;

		border-radius: 100%;
		width: 80px;
		height: 80px;

		background-color: var(--color-primary);

		transform: translate(-50%, -50%);
		z-index: 4;

		svg{
			fill: var(--color-light);
			width: 26px;
			height: 26px;

			margin-right: -4px;
		}
	}

	&__muted{
		@include flex-all-center;

		position: absolute;

		bottom: 32px;
		left: 32px;

		border-radius: 100%;
		width: 40px;
		height: 40px;

		background-color: rgba($color: #fff, $alpha: .12);

		z-index: 4;

		svg{
			fill: var(--color-light);
			width: 20px;
			height: 20px;
		}
	}
}