.header-contacts{
	@include flex-v-center;

	
	&__link{
		@include flex-collumn-start;

		&:not(:last-child){
			margin-right: 36px;
		}
	}
}

.contacts-link {
	padding-left: 52px;

	background-position: left center;
	background-repeat: no-repeat;
	background-size: 40px 40px;

	&__caption{
		font-weight: 700;
		font-size: 14px;
		line-height: 150%;

		color: var(--color-gray);
	}

	&__value{
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;

		color: #1E212C;
	}

	&--call{
		background-image: url(../img/iPhone.svg);
	}

	&--mail{
		background-image: url(../img/Mail.svg);
	}
}
