html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,h2,h3,h4,h5,h6,p{
	margin: 0;
	padding: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;

}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


.main-title{
	margin: 0;
	margin-bottom: 24px;
	text-transform: uppercase;
	font-weight: 700;
	font-size: 72px;
	line-height: 130%;
	letter-spacing: 1px;

	color: var(--color-ebony);

	span{
		color: var(--color-primary);
	}

	&--home{
		color: var(--color-light);
	}
}

.main-descr{
	margin: 0;

	font-weight: 400;
	font-size: 20px;
	line-height: 150%;

	color: var(--color-storm);

	&--home{
		color: var(--color-light);
		margin-bottom: 61px;
	}
}

.btn{
	border-style: solid;
	border-width: 1px;
	border-color: transparent;

	font-weight: 700;
	font-size: 16px;
	line-height: 20px;

	text-align: center;
	letter-spacing: 0.5px;
	text-transform: uppercase;

	color: var(--color-light);

	border-radius: 4px;

	padding: 15.1px 38.2px;

	&:not(:last-child){
		margin-right: 20px;
	}

	&--fill{
		background-color: var(--color-primary);
		padding-top: 16px;
		padding-right: 41px;
	}

	&--stroke{
		color: var(--color-primary);

		&-primary{
			border-color: var(--color-primary);
		}

		&-light{
			border-color: var(--color-light);
			color: var(--color-light);
		}
	}
}

.title{
	font-weight: 700;
	font-size: 46px;
	line-height: 130%;

	color: var(--color-ebony);

	margin-bottom: 23px;

	&--centr{
		text-align: center;
	}

	&--left{
		text-align: left;
	}
}

.descr{
	font-weight: 400;
	font-size: 18px;
	line-height: 150%;

	text-align: center;

	color: var(--color-storm);

	margin-bottom: 60px;

	&--centr{
		text-align: center;
	}
}


.section-offset{
	padding-top: 121px;
}

