.services-section{
	padding-top: 212px;
	padding-bottom: 80px;
	background-size: cover;
	background-position: center ;
	background-repeat: no-repeat;
	background-image: url("../img/bg-image-services.jpg");
	// background-color: var(--color-storm); 

	&__list{
		margin-bottom: 78px;
	}
}

.services-list{
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: var(--grid-gap);

}

.services-item{
	position: relative;
	display: block;
	padding: 90px 10px;


	border: 1px solid #E5E8ED;
	border-radius: 4px;

	box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
	0px 30px 24px -10px rgba(154, 156, 165, 0.05),
	0px 12px 10px -6px rgba(154, 156, 165, 0.04),
	0px 4px 4px -4px rgba(30, 33, 44, 0.03);

	background: var(--color-light);


	// style for img with :hover
	&::before{
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 3;

		border-radius: inherit;

		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		
		background-color: var(--color-ebony);

		opacity: 0;
		transition: opacity .3s ease-in-out;
	}


	// overlay gray
	&::after{
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 4;

		border-radius: inherit;

		background-color: rgba($color: #1E212C, $alpha: 0.6);

		opacity: 0;
		transition: opacity .3s ease-in-out;
	}

	&__content{
		@include flex-collumn-centr;

		position: relative;
		z-index: 10;
	}

	&__icon{
		fill: var(--color-primary);

		width: 52px;
		height: 52px;
		object-fit: cover;

		margin-bottom: 24px;

		transition: fill .3s ease-in-out;
	}

	&__title{
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		text-align: center;

		color: var(--color-ebony);

		transition: color .3s ease-in-out;
	}

	&--construction{
		&::before{
			background-image: url("../img/services-1.jpg");
		}
	}

	&:hover{
		&::before{
			opacity: 1;
		}

		&::after{
			opacity: 1;
		}

		.services-item__icon{
			fill: var(--color-light);
		}

		.services-item__title{
			color: var(--color-light);
		}
	}
}