.facts{
	padding-top: 181px;
	padding-bottom: 150px;
	background-color: var(--color-light);

	background-image: url("../img/bg-image-section-facts.png");
	background-repeat: no-repeat;
	background-position: left top;

	&__title{
		margin-bottom: 60px;
	}

	&__list{
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		margin: 0 auto;
		gap: 48px;
		max-width: 736px;
	}
}

.facts-element{
	&__circle{
		width: 148px;
		height: 148px;
		position: relative;

		margin-bottom: 20px;

		svg{
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}

		.progress{
			overflow: hidden;
			transform: rotate(-90deg) translateX(20px);
			transform-origin: center;
		}
	}
	
	&__value{
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		font-weight: 700;
		font-size: 32px;
		line-height: 150%;

		color: var(--color-ebony);

	}

	&__text{
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		text-align: center;
		color: var(--color-ebony);
	}
}